import React, { useEffect, useState } from "react";
import { useStores } from "../stores";
import InfiniteScroll from "react-infinite-scroller";
import { Button, List, Spin } from "antd";
import styled from "styled-components";
import { observer } from "mobx-react";
import products from "../assets/product.json";
import { useHistory } from "react-router-dom";
import { UpLoader } from "../models";
const Img = styled.img`
  width: 100px;
  height: 120px;
  object-fit: contain;
  border: 1px solid #eee;
`;
const Div = styled.div`
display:flex;
flex-direction:row-reverse
`;
const CheckButton = styled(Button)`
margin-left:20px`;
const Component = observer(() => {
  const { HistoryStore } = useStores();
  const history = useHistory();
  const loadMore = () => {
    HistoryStore.find("Cart");
  };
  var money = 0;
  const pageTransfer = () => {
    history.push({
      pathname: "/payment",
      state: { price: totalMoney },
    });
    setTotalMoney(0);
  };
  const [totalMoney, setTotalMoney] = useState(0);
  useEffect(() => {
    return () => {
      HistoryStore.reset();
    };
  }, []);
  return (
    <div>
      <InfiniteScroll
        initialLoad={true}
        pageStart={0}
        loadMore={loadMore}
        hasMore={!HistoryStore.isLoading && HistoryStore.hasMore}
      >
        <List
          dataSource={HistoryStore.list}
          renderItem={(item) => (
            <List.Item key={item.id}>
              <div>
                <Img
                  height={120}
                  width={100}
                  src={
                    products[item.attributes.type][item.attributes.No]
                      .photoAddress
                  }
                />
              </div>
              <div>
                <h4>
                  {products[item.attributes.type][item.attributes.No].name}
                </h4>
              </div>
              <div>
                <h5>
                  ${products[item.attributes.type][item.attributes.No].price}
                  <div style={{ display: "none" }}>
                    {
                      (money += parseInt(
                        products[item.attributes.type][item.attributes.No].price
                      ))
                    }
                  </div>
                  {setTotalMoney(money)}
                </h5>
              </div>
              <div>
                <h5>quantity : haimeizuo</h5>
              </div>
              <div>
                <Button
                  type="danger"
                  onClick={async () => {
                    await UpLoader.delete(item.id);
                    window.location.reload();
                  }}
                >
                  Delete
                </Button>
              </div>
            </List.Item>
          )}
        >
          {HistoryStore.isLoading && HistoryStore.hasMore && (
            <div>
              <Spin  style={{marginLeft:"50%",transform:"translateX(-50%)"}} size={"large"} tip="loading" />
            </div>
          )}
        </List>
      </InfiniteScroll>
      <Div>
      <CheckButton type="primary" size={"large"} onClick={pageTransfer}>
        CHECKOUT
      </CheckButton>
        <h1>Total: {totalMoney} </h1>
      </Div>
    </div>
  );
});

export default Component;
